import '../FAQSection/FAQSection.scss'
import '../FAQSection/FAQSection950.scss'
import '../FAQSection/FAQSection750.scss'
import '../FAQSection/FAQSectionMobile.scss'
import FAQSectionButton from '../FAQSectionButton/FAQSectionButton';
import FAQTab from '../FAQTab/FAQTab';
import { useState } from 'react';
import { motion } from 'framer-motion'; 
import { useMediaQuery } from 'react-responsive';

const FAQSection = () => {

    const isTablet1050 = useMediaQuery({ maxWidth: 1051 })

    const [showAll, setShowAll] = useState(false);
    
    const faqItems = [
        {
            question: '     ?',
            answer: '   ,             ,       !'
        },
        {
            question: '       ?',
            answer: ' .   ,     ,      .'
        },
        {
            question: '    ,  ?',
            answer: '!     ,   ,       .       . ,   ,      ,   ,   .      ,     1 ,    , .      ,        .'
        },
        {
            question: ',     ?',
            answer: ' .        .      .'
        },
        {
            question: ',      ?',
            answer: '   ,      . , ,    ,     .'
        },
        {
            question: ' ?',
            answer: ' ,   .   ,  .'
        },
        {
            question: '      ?',
            answer: '  ,        .        .     .     ,      .'
        },
        {
            question: '  ,      ?',
            answer: ',     ,           .          .'
        },
        {
            question: '  ?',
            answer: ' .  ,  .          ,    ,         .     ,         .'
        },
        {
            question: '      ?',
            answer: ',   ,    ,    .        ,    ,    .  -  .'
        },
        {
            question: '    , , ,     ?',
            answer: ',   ,  .       .     ,           .        ,     .'
        },
        {
            question: '   ?',
            answer: '!    ,        ( 100%   12 ).    ,         .     ,     .'
        },
        {
            question: ' .',
            answer: '         ,       .       ,     .     ,    ? ,        ,   ?'
        },
        {
            question: ' .',
            answer: '   , ,     .   ,   .  4           ,    ",      ,    ,       .    ,  ,  .  ,      .        ,   .'
        },
        {
            question: '   !',
            answer: ',      .    : ,  ,    ,     .        .   ,    ,       .'
        },
        {
            question: '   ?',
            answer: ' :   ~4.000  .12    - 48.000+      (          )       ,    !'
        },
        {
            question: '     ?',
            answer: ',       .'
        }
    ];

    const visibleItems = showAll ? faqItems : faqItems.slice(0, 6);

    const toggleShowAll = () => {
        setShowAll(!showAll);
    };

    return(
        <section className='FAQSection'>
            <div className='container'>
                {isTablet1050 ? (
                    <div className='saleSectionName'>
                        <h1 className='FAQSectionTitle'> ?</h1>
                    </div>
                ) : (
                    <motion.div 
                        className='saleSectionName'
                        initial={{ opacity: 0 }}
                        whileInView={{ opacity: 1 }}
                        viewport={{ once: true }}
                        transition={{ duration: 0.8 }}
                    >
                        <h1 className='FAQSectionTitle'> ?</h1>
                    </motion.div>
                )}
                <div className='FAQSectionTabs' id='FAQSection'>
                    {visibleItems.map((item, index) => (
                        <FAQTab 
                            key={index}
                            question={item.question}
                            answer={item.answer}
                        />
                    ))}
                </div>
                {faqItems.length > 6 && (
                    <FAQSectionButton 
                        onClick={toggleShowAll} 
                        showAll={showAll}
                    />
                )}
            </div>
        </section>
    )
}

export default FAQSection;