import ExpertBanner from '../ExpertBanner/ExpertBanner';
import '../WhoWillHelpSection/WhoWillHelpSection.scss';
import '../WhoWillHelpSection/WhoWillHelpSection1050.scss';
import '../WhoWillHelpSection/WhoWillHelpSection750.scss';
import '../WhoWillHelpSection/WhoWillHelpSectionMobile.scss';
import { useState } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import { useMediaQuery } from 'react-responsive';

import diplomIconBlue from '../../../../assets/images/icons/diplomIconBlue.svg';
import diplomIconOrange from '../../../../assets/images/icons/diplomIconOrange.svg';
import dumbbellIconBlue from '../../../../assets/images/icons/dumbbellIconBlue.svg';
import dumbbellIconOrange from '../../../../assets/images/icons/dumbbellIconOrange.svg';
import cupIcon from '../../../../assets/images/icons/cupIcon.svg';
import starIcon from '../../../../assets/images/icons/starIcon.svg'

import nikitaResult from '../../../../assets/images/pictures/nikitaResult.png'
import uriResult from '../../../../assets/images/pictures/uriResult.png'
import aboutMePopUpBackground from '../../../../assets/images/pictures/aboutMePopUpBackground.png'

import orangeQuotes from '../../../../assets/images/icons/orangeQuotes.svg'
import blueQuotes from '../../../../assets/images/icons/blueQuotes.svg'

import LinkPageButton from '../LinkPageButton/LinkPageButton';
import AboutMeButton from '../AboutMeButton/AboutMeButton';
import AboutMePopUp from '../AboutMePopUp/AboutMePopUp';

const WhoWillHelpSection = () => {

    const isTablet1050 = useMediaQuery({ maxWidth: 1051 })
    const isMobile = useMediaQuery({ maxWidth: 451 })

    const nikitaSkillsItems = [
        { text: '  ',icon: diplomIconBlue },
        { text: '   ',icon: diplomIconBlue },
        { text: ' 5     ',icon: dumbbellIconBlue },
        { text: '20    ',icon: dumbbellIconBlue },
        { text: '   ,   skytech',icon: dumbbellIconBlue },
        { text: '      ',icon: cupIcon },
        { text: '      ',icon: cupIcon }
    ]

    const uriSkillsItems = [
        { text: '  ', icon: diplomIconOrange },
        { text: '   ', icon: diplomIconOrange },
        { text: '8    ', icon: dumbbellIconOrange },
        { text: ' 18    ', icon: dumbbellIconOrange },
        { text: ' 16    ', icon: dumbbellIconOrange },
        { text: ',    ,   ,   ', icon: starIcon },
    ]

    const [isPopUpOpen, setIsPopUpOpen] = useState(false);

    const openPopUp = () => setIsPopUpOpen(true);
    const closePopUp = () => setIsPopUpOpen(false);

    const aboutMePopUpItems = [
        { text: '          ' },
        { text: '        ,     ' },
        { text: '         .        ' },
        { text: '         ' },
    ];
    const aboutMePopUpItemsAchives = [
        { text: '   ' },
        { text: '      ' },
        { text: '1     ' },
        { text: '5-      ' },
        { text: '      ' }
    ];

    return (
        <section className='whoWillHelpSection'>
            {isTablet1050 ? (

                <div className='container'>
                    <h1 className='whoWillHelpSectionTitle'>
                           ?
                    </h1>
                    <ExpertBanner 
                        color={'#1257E0'}
                        title={' '}
                        subtitle={'   '}
                        image={nikitaResult}
                        quotes={blueQuotes}
                        quote={'        14     5           '}
                        description={'        .'}
                        items={nikitaSkillsItems}
                    >
                        <AboutMeButton onClick={openPopUp} />
                    </ExpertBanner>
                
                    <ExpertBanner 
                        color={'#FF6B16'}
                        title={' '}
                        subtitle={'   '}
                        image={uriResult}
                        quotes={orangeQuotes}
                        quote={'     ,   31       '}
                        description={'   , ,      .'}
                        items={uriSkillsItems}
                    />  
                    {isMobile ? (
                        <LinkPageButton
                            width={'100%'}
                            height={62}
                            background={'#000'}
                            color={'#fff'}
                            fontSize={18}
                            fontWeight={600}
                            page={'/experts'}
                            text={'  '}
                            hoverBackground={'#2471FF'}
                        />
                    ) : (
                        <LinkPageButton
                            width={322}
                            height={62}
                            background={'#000'}
                            color={'#fff'}
                            fontSize={18}
                            fontWeight={600}
                            page={'/experts'}
                            text={'  '}
                            hoverBackground={'#2471FF'}
                        />
                    )}
                    {isMobile ? (
                        <AnimatePresence>
                            {isPopUpOpen && (
                                <>
                                    <motion.div
                                        className='overlay'
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 0.6 }}
                                        exit={{ opacity: 0 }}
                                        transition={{ duration: 0.5 }}
                                        onClick={closePopUp}
                                    />

                                    <AboutMePopUp
                                        description={'                  .     ,           '}
                                        itemsType={aboutMePopUpItems}
                                        onClose={closePopUp}
                                        itemsAchives={aboutMePopUpItemsAchives}
                                        image={aboutMePopUpBackground}
                                        isMobile={isMobile}
                                    />
                                </>
                            )}
                        </AnimatePresence>
                    ) : (
                        <AnimatePresence>
                            {isPopUpOpen && (
                                <>
                                    <motion.div
                                        className='overlay'
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 0.6 }}
                                        exit={{ opacity: 0 }}
                                        transition={{ duration: 0.5 }}
                                        onClick={closePopUp}
                                    />

                                    <AboutMePopUp
                                        description={'                  .     ,           '}
                                        itemsType={aboutMePopUpItems}
                                        onClose={closePopUp}
                                        itemsAchives={aboutMePopUpItemsAchives}
                                        image={aboutMePopUpBackground}
                                        isMobile={isMobile}
                                    />
                                </>
                            )}
                        </AnimatePresence>
                    )}
                    
                </div>

            ) : (

                <div className='container'>
                    <motion.h1 
                        className='whoWillHelpSectionTitle'
                        initial={{ opacity: 0 }}
                        whileInView={{ opacity: 1 }}
                        viewport={{ once: true }}
                        transition={{ duration: 0.8, ease: "easeOut" }}
                    >
                           ?
                    </motion.h1>
                    <div className='whoWillHelpSectionContent'>
                        <motion.div
                            initial={{ opacity: 0, x: -100 }}
                            whileInView={{ opacity: 1, x: 0 }}
                            viewport={{ once: true }}
                            transition={{ duration: 0.8, ease: "easeOut" }}
                        >
                            <ExpertBanner 
                                color={'#1257E0'}
                                title={' '}
                                subtitle={'   '}
                                image={nikitaResult}
                                quotes={blueQuotes}
                                quote={'        14     5           '}
                                description={'        .'}
                                items={nikitaSkillsItems}
                            >
                                <AboutMeButton onClick={openPopUp} />
                            </ExpertBanner>
                        </motion.div>
                        <motion.div
                            initial={{ opacity: 0, x: 100 }}
                            whileInView={{ opacity: 1, x: 0 }}
                            viewport={{ once: true }}
                            transition={{ duration: 0.8, ease: "easeOut", delay: 0.2 }}
                        >
                            <ExpertBanner 
                                color={'#FF6B16'}
                                title={' '}
                                subtitle={'   '}
                                image={uriResult}
                                quotes={orangeQuotes}
                                quote={'     ,   31       '}
                                description={'   , ,      .'}
                                items={uriSkillsItems}
                            />
                        </motion.div>
                    </div>
                    <motion.div
                        initial={{ opacity: 0, y: 50 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                        transition={{ duration: 0.8, ease: "easeOut", delay: 0.4 }}
                        style={{ display: 'flex', justifyContent: 'center' }}
                    >
                        <LinkPageButton
                            width={322}
                            height={62}
                            background={'#000'}
                            color={'#fff'}
                            fontSize={18}
                            fontWeight={600}
                            page={'/experts'}
                            text={'  '}
                            hoverBackground={'#2471FF'}
                        />
                    </motion.div>
                    <AnimatePresence>
                        {isPopUpOpen && (
                            <>
                                <motion.div
                                    className='overlay'
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 0.6 }}
                                    exit={{ opacity: 0 }}
                                    transition={{ duration: 0.5 }}
                                    onClick={closePopUp}
                                />

                                <AboutMePopUp
                                    description={'                  .     ,           '}
                                    itemsType={aboutMePopUpItems}
                                    onClose={closePopUp}
                                    itemsAchives={aboutMePopUpItemsAchives}
                                    image={aboutMePopUpBackground}
                                    isMobile={isMobile}
                                />
                            </>
                        )}
                    </AnimatePresence>
                </div>

            )}
        </section>
    );
};

export default WhoWillHelpSection;